import CIcon from '@coreui/icons-react';
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react';
import React, { useState } from 'react';
import FormDate from 'src/components/form-inputs/form-date.component';
import FormGenderSelect from 'src/components/form-inputs/form-gender-select.component';
import CountryCodeEnum from 'src/components/goaml-enums/country-code-enum/country-code-enum.component';
import ReportAddresses from 'src/components/goaml-sections/report-addresses/report-addresses.component';
import ReportIdentifications from 'src/components/goaml-sections/report-identifications/report-identifications.component';
import ReportPhones from 'src/components/goaml-sections/report-phones/report-phones.components';

const Register = () => {
  const initialState = {userName: '', email: '', password: '', repeatPassword: ''};
  const [isState, setIsState] = useState(initialState);
  const {userName, email, password, repeatPassword} = isState;
  const [error, setError] = useState({userName: false, email: false, password: false, repeatPassword: false });


  const handleInputs = (e) => {
    setError({ ...error,  })
    if (e.target.name === 'userName') {
      e.target.value === '' ? setError({...error, userName: true}) : setError({...error, userName: false}) 
    } else if (e.target.name  === 'email') {
      e.target.value === '' ? setError({...error, email: true}) : setError({...error, email: false}) 
    } else if (e.target.password  === repeatPassword) {
      e.target.value === '' ? setError({...error, password: true}) : setError({...error, password: false}) 
    }
    setIsState({ ...isState, [e.target.name]: e.target.value });
  }

  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="12" lg="12" xl="12">
            <CCard className="mx-4">
              <CCardBody className="p-4">
                <CForm>
                  <h1>Register</h1>
                  <p className="text-muted">Create your account</p>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-user" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput 
                      type="text" 
                      placeholder="Username" 
                      autoComplete="username"
                     className={`form-control ${error.userName ? 'is-invalid' : 'is-valid'}`} 
                     value={userName} 
                     name="userName" 
                     onChange={handleInputs} required />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                     <CInputGroupPrepend>
                       <CInputGroupText>
                         <CIcon name="cil-user" />
                       </CInputGroupText>
                     </CInputGroupPrepend>
                     <CInput 
                        type="text"
                        placeholder="First Name*"
                     />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                     <CInputGroupPrepend>
                       <CInputGroupText>
                         <CIcon name="cil-user" />
                       </CInputGroupText>
                     </CInputGroupPrepend>
                     <CInput 
                        type="text"
                        placeholder="Last Name*"
                     />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                     <CInputGroupPrepend>
                       <CInputGroupText>
                         <CIcon name="cil-user" />
                       </CInputGroupText>
                     </CInputGroupPrepend>
                     <CInput 
                        type="text"
                        placeholder="Organisation ID*"
                     />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                     <CInputGroupPrepend>
                       <CInputGroupText>
                         #
                       </CInputGroupText>
                     </CInputGroupPrepend>
                     <CInput 
                        type="text"
                        placeholder="ssn*"
                     />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                     <CInputGroupPrepend>
                       <CInputGroupText>
                         #
                       </CInputGroupText>
                     </CInputGroupPrepend>
                     <CInput 
                        type="text"
                        placeholder="Occupation"
                     />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                     <CInputGroupPrepend>
                       <CInputGroupText>
                         #
                       </CInputGroupText>
                     </CInputGroupPrepend>
                     <CInput 
                        type="text"
                        placeholder="Title"
                     />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>@</CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput 
                      type="text" 
                      placeholder="Email" 
                      autoComplete="email" 
                      className={`form-control ${error.email ? 'is-invalid' : 'is-valid'}`} 
                      value={email} 
                      name="email" 
                      onChange={handleInputs} required />
                     {/* {error && (
                    <div className="invalid-fedback">email is required</div>
                     )} */}
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-lock-locked" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput 
                      type="password" 
                      placeholder="Password" 
                      autoComplete="new-password"
                      className={`form-control ${error.password ? 'is-invalid' : 'is-valid'}`} 
                      value={password} 
                      name="password" 
                      onChange={handleInputs} required />
                  </CInputGroup>
                  <CInputGroup className="mb-4">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-lock-locked" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                      <CInput 
                      type="password" 
                      placeholder="Repeat password" 
                      autoComplete="new-password"
                      className={`form-control ${error.password ? 'is-invalid' : 'is-valid'}`} 
                      value={password} 
                      name="password" 
                      onChange={handleInputs} required />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                      <FormDate 
                        type="date"
                        placeholder="Birth Date"
                      />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                      <FormGenderSelect 
                        label="Gender" 
                        name="gender" 
                      />
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                      <CountryCodeEnum
                        label="Gender" 
                        name="Nationality" 
                      />
                  </CInputGroup>
                  <CInputGroup>
                     <CCol xs="12">
                    <ReportPhones
                    label="Phone" 
                    name="phones" 
                    >
                    </ReportPhones>
                    </CCol>
                    <CCol xs="12">
                    <ReportAddresses
                    label="Address" 
                    name="addresses" 
                    >
                    </ReportAddresses>
                    </CCol>
                    <CCol xs="12">
                    <ReportIdentifications 
                    label="Identification" 
                    name="identification" 
                    >
                    </ReportIdentifications>
                    </CCol>                
                  <CButton color="success" block>Create Account</CButton>
                  </CInputGroup>
                </CForm>
              </CCardBody>
              <CCardFooter className="p-4">
                <CRow>
                  <CCol xs="12" sm="6">
                    <CButton className="btn-facebook mb-1" block><span>facebook</span></CButton>
                  </CCol>
                  <CCol xs="12" sm="6">
                    <CButton className="btn-twitter mb-1" block><span>twitter</span></CButton>
                  </CCol>
                </CRow>
              </CCardFooter>
            </CCard>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Register
